import Vue from 'vue'
import Router from 'vue-router'
import index from '@/pages/index'
import shop from '@/pages/shop'
import book from '@/pages/book'
import resume from '@/pages/resume'
import blog from '@/pages/blog'
import select from '@/pages/select'

Vue.use(Router)

export default new Router({
  mode:'history',
  routes: [
    {
      path: '/',
      name: 'index',
      component: index
    },
    {
      path: '/shop',
      name: 'shop',
      component: shop
    },
    {
      path: '/book',
      name: 'book',
      component: book
    },
    {
      path: '/resume',
      name: 'resume',
      component: resume
    },
    {
      path: '/blog',
      name: 'blog',
      component: blog
    },
    {
      path: '/select',
      name: 'select',
      component: select
    }
  ]
})



